import request from '@/utils/request.js';

//
export function getSelectOption(callback = null) {
    return request({
        url: '/question/getSelectOption',
        method: 'get',
    }, callback)
}

//
export const uploadPicture = {
    baseUrl:process.env.VUE_APP_BASEURL,
    url: process.env.VUE_APP_BASEURL + '/uploads',//
    name: 'image', //
    acceptType: ['jpg', 'jpeg', 'png', 'gif'],//
    sizeLimit: 4 * 1024 * 1024,// B
}

//
export const uploadVideo = {
    baseUrl:process.env.VUE_APP_BASEURL,
    url: process.env.VUE_APP_BASEURL + '/question/upload',//
    name: 'file', //
    acceptType: ['mp4', 'avi'],//
    sizeLimit: 100 * 1024 * 1024,// B
}