//

import request from '@/utils/request.js';


//
export function getQuestionList(data = {}, callback = null) {
    return request({
        url: '/question/list',
        method: 'get',
        params: data
    }, callback)
}

//
export function getSelectOption(callback = null) {
    return request({
        url: '/question/getSelectOption',
        method: 'get',
    }, callback)
}

//
export function setStatus(data = {}, callback = null) {
    return request({
        url: '/question/status',
        method: 'post',
        data: data
    }, callback)
}

//
export function del(data = {}, callback = null) {
    return request({
        url: '/question/remove',
        method: 'post',
        data: data
    }, callback)
}
//
export function add(data = {}, callback = null) {
    return request({
        url: '/question/add',
        method: 'post',
        data: data
    }, callback)
}
//
export function edit(data = {}, callback = null) {
    return request({
        url: '/question/edit',
        method: 'post',
        data: data
    }, callback)
}

//
export function getInfo(data = {}, callback = null) {
    return request({
        url: '/info',
        method: 'get',
        params: data
    }, callback)
}



